import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import { toKebabCase } from '../helpers';

// import style from '../styles/post.module.css';
import style from '../styles/tag.module.css';

const TagList = ({ tagInfos }) => {
  return (
    <div className={style.container}>
      <div className={style.tags}>
        {tagInfos &&
          tagInfos.map(tagInfo => (
            <Link
              className={style.tag}
              to={`/post/tag/${toKebabCase(tagInfo.fieldValue)}/`}
              key={toKebabCase(tagInfo.fieldValue)}
            >
              <span>
                #{tagInfo.fieldValue} ({tagInfo.totalCount})
              </span>
            </Link>
          ))}
      </div>
    </div>
  );
};

TagList.propTypes = {
  tagInfos: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default TagList;
